<style scoped>
#commit{
  position: relative;
  background-color: #ffffff;
  margin-top: 20px;/*px*/
  overflow: hidden;
  text-align: center;
}

#commit div{
      position: relative;
      width: 240px;/*px*/
      padding: 20px 20px 20px 20px;/*px*/
      display: inline-block;
      font-size: 32px;/*px*/
      text-align: center;
      background-color: #46c9d8;
      border-radius: 8px;
      color: #ffffff;
      margin-top: 20px;/*px*/
      margin-left: 40px;/*px*/
      -webkit-transition: background-color 1s;
}

#commit div:hover{
    background-color: #33a5b2;
}

#commit #first-btn{
    margin-left: 0px;

}

#second-btn{

}
</style>

<template>
<div id="commit">
        <div id="first-btn" @click="firstBtn">{{first}}</div>
        <div id="second-btn">{{second}}</div>
</div>
</template>

<script>
  module.exports = {
    props: [''],
    components: {
    },
    data: function () {
      return {
        isJoin: false,
        first: "参与",
        second: "待定",
      }
    },
    methods: {
        firstBtn: function(){
            var that = this;
            if(!this.isJoin){
                that.first = "不参与";
                that.isJoin = true;
                that.second = "邀请";
            }else{
                that.first = "参与";
                that.isJoin = false;
                that.second = "待定";
            }
        },
    },
    ready: function () {
    }
  }
</script>



